.foott{
    font-size: 40px;
    font-weight: 700;
    color: rgb(23, 105, 110);
    letter-spacing:"10px";
    word-spacing:"10px";
    text-shadow: 0 0 5px rgb(23, 105, 110);
}

@media(max-width:480px){
    .foott{
        font-size: 20px;
    }
}
@media(max-width:350px){
    .foott{
        font-size: 15px;
        letter-spacing:"2px";
        word-spacing:"2px";
    }
}