.cardd {
    box-shadow: 0 0 5px snow;
    width: calc(20% - 16px);
    max-height: 400px;
    position: relative;
    margin-left: 8px;
    margin-right: 8px;
    margin-bottom: 15px;
    box-sizing: border-box;
    border-radius: 5px;
}
.imgC {
    width: 100%;
    height: 100%;
    border-radius: 5px;
}
.imgC > img {
    width: 100%;
    height: 100%;
    border-radius: 5px;
}
.infoC {
    opacity: 0;
    background-color: black;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    transition: opacity 1s;
}
.infoCC {
    display: block;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    opacity: 0.7;
    background-color: black;
    box-shadow: 0 0 5px snow, 0 0 10px gold;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1%;
    z-index: 10;
    transition: opacity 700ms;
}
.infoCC > p {
    letter-spacing: 3px;
    color: gold;
    text-align: center;
    text-transform: capitalize;
    margin-top: 0;
    margin-bottom: 0;
    padding: 2%;
    border-bottom: 1px solid rgba(218, 165, 32, 0.342);
    border-radius: 5px;
    z-index: 10;
}

@media (max-width: 900px){
    .cardd {
        width: calc(25% - 16px);
    }
}
@media (max-width: 768px){
    .cardd {
        width: calc(33.3% - 16px);
    }
}
@media (max-width: 582px){
    .cardd {
        width: calc(50% - 16px);
    }
}
@media (max-width: 350px){
    .cardd {
        width: calc(100% - 16px);
    }
}