.nav1 {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(42, 78, 99);
    margin-bottom: 10px;
    padding: 1%;
    border-radius: 10px;
    box-shadow: 0 0 5px rgb(42, 78, 99);
}
.nav1 > p{
    letter-spacing: 5px;
    color: gold;
    font-size: 40px;
    margin: 0;
    margin-bottom: 5px;
    font-weight: 900;
    text-shadow: 0 0 10px goldenrod;
}
.inputW {
    width: 40%;
    padding: 1%;
}
.inputW > input {
    width: 98%;
    margin-left: auto;
    margin-right: auto;
    font-size: 20px;
    border: 0;
    outline: 0;
    border-radius: 5px;
    box-shadow: 0 0 5px snow;
    padding: 1%;
}
.inputW > input::placeholder {
    color: rgba(0, 0, 0, 0.473);
}

@media(max-width:900px){
    .inputW {
        width: 50%;
    }
}
@media(max-width:550px){
    .inputW {
        width: 60%;
    }
    .nav1 > p{
        font-size: 35px;
    }
}
@media(max-width:440px){
    .nav1{
        flex-direction: column;
    }
    .nav1 > p {
        font-size: 40px;
        letter-spacing: 15px;
    }
    .inputW {
        width: 90%;
    }
}
@media(max-width:300px){
    .nav1 > p{
        font-size: 30px;
    }
}